			<div class="mindset_note">
				<h4 class="pri-text">PM對UX的影響</h4>
				產品的開發需要團隊的合作，並非UX Designer一人能使一切完美。<br>
				PM扮演著重要的因素， 良好的給予設計 開發節奏，才能有創造卓越的空間。<br>
				在我經歷過的公司中，PM擁有第一時間與老闆商議專案可行性的機會，能主導將開發計畫拆分成分配發包的規模，減少設計與開發面臨的需求大改風險，並能獲得使用者需求與反饋。<br><br>
				相反的，不好的PM會講需求與反饋內藏，不分享給設計與開發，並且對老闆的需求照單全收，此時位於流水線後端的UX UI，會被迫基於先天不良的主題做設計。
			</div>
