			<div class="mindset_note">
				<h4 class="pri-text">Prototype工具的選用心得</h4>

				我主要使用Axure RP或選寫HTML/CSS/JS來建立Prototype，也使用過Marvel, Invision。
				<br>
				Axure可以幫助我摸擬複雜的換頁流程，也可以模擬出中等複雜的互動，例如：一個頁面中有多個捲動區域、Anchor Link、捲動一定距離(200px)才開始令某元素置頂。
				<br>
				選寫HTML/CSS/JS最為費時，我用來解決高複雜的互動效果or UI，例如drag & drop編輯功能、支援RWD的table layout。在複雜度與內容多樣性達到一定程度後，用不能轉為程式碼的工具來做prototype，反而不利於改版及開發。

				<br>
