				<h4 class="pri-text">User Research的時機與方法</h4>
				
				我目前認知到的軟體User Experience可分為：①使用者需求的滿足 ②UI的Usability 
				<br />
				兩者的重要性，在不同的產品上會有相異的比重 -- <br />
				對B2C軟體而言，由於需求的功能較少，不易和競品做出差異，所以Usability的優劣是一大重點；<br />
				而對B2B軟體來說，需求功能往往是多樣、且有多種使用者以不同的權限在操作，光是使用者需求的滿足上，就足以拉出差距。
				<br /><br />
				身為一個UX Designer，需要兼顧兩者。<br />
				在傳統瀑布式的設計開發(UCD)中，使用者需求的調查在於設計前期；<br />
				而設計出來的UI usability則在設計完成~開發完成中間測試。<br />
				至於使用者們的真實回饋則有賴Release後的數據、滿意度蒐集。<br />
