				<div class="p-title">
					User Feedback & 持續迭代
				</div>
				
				在蒐集使用者回饋上，我們採用第三方的量化工具 - GA / hotjar:
				<br/><br/>
				<div class="p-title-sub">
					使用GA觀察長期數據趨勢
				</div>
				<ul>
					<li>
						依使用者瀏覽過的網址做內容分類 - 觀察使用者在各頁面分佈的比例，可判斷User重點使用的功能或操作路徑。設定方式：GA的資料檢視→內容分組，可直接設定將符合規則的多個網址歸類為同一組(EX:不同資料的相同報表頁面，網址僅參數不同，可視為同一組)
					</li>
					<li>
						事件追蹤 - 跨頁面的統計各功能(按鈕、超連結)的使用量差異與趨勢。透過 GoogleTagManager，我可以盡量不靠RD便能即時加設追蹤碼，在使用者點擊網頁上特定按鈕時發送事件，並於GA做追蹤
					</li>
					<li>
						UserID追蹤 - 統計各個使用者造訪的次數與趨勢。GA僅能辨識在同一裝置同瀏覽器造訪的使用者，但透過前端RD的協助與GTM設定，能在User登入時將系統所記ID發送給GA歸戶，從而跨瀏覽器、跨裝置的辨識出登入的使用者
					</li>
				</ul>
				
				
