import Vue from 'vue'
import Router from 'vue-router'
import Project from './views/Project.vue'
import Blog from './views/Blog.vue'
import ThreeJs from './views/ThreeJs.vue'
import Mindset from './views/Mindset.vue'
import Mos_Redesign from './views/Projects/Mos_Redesign.vue'
import OneAD_OAM from './views/Projects/OneAD_OAM.vue'
import OneAD_ODM from './views/Projects/OneAD_ODM.vue'
import OneAD_QP from './views/Projects/OneAD_QP.vue'
import OneAD_DesignOps from './views/Projects/OneAD_DesignOps.vue'
import LD_HCM from './views/Projects/LD_HCM.vue'

Vue.use(Router)


export default new Router({
  // mode: 'hash',
  mode: 'hash',
  // mode:history時，網址不會帶#字
  // 当 <router-link> 对应的路由匹配成功，将自动设置 class 属性值 .router-link-active
  // 2020發現，切history mode 會造成我在github page上按refresh會404的狀況
  routes: [
    
    // { path: '/', redirect: '/project' },
    
    {
      path: '/',
      name: 'Outer_Frame',
      meta:{depth:1},
      component: () => import(/* webpackChunkName: "about" */ './views/Outer_Frame.vue'),
      children: [
        {
          path: '/',
          name: 'Project',
          meta:{depth:1},
          component: Project
        },
        {
          path: '/about',
          name: 'about',
          meta:{depth:2},
          // route level code-splitting
          // this generates a separate chunk (about.[hash].js) for this route
          // which is lazy-loaded when the route is visited.
          component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        },
        {
          path: '/ThreeJs',
          name: 'ThreeJs',
          meta:{depth:2.5},
          component: ThreeJs
        },
        {
          path: '/blog',
          name: 'blog',
          meta:{depth:3},
          component: Blog
        }
      ]
    },
    {
      path: '/Mos_Redesign',
      name: 'Mos_Redesign',
      meta:{depth:4},
      component: Mos_Redesign
    },
    {
      path: '/OneAD_OAM',
      name: 'OneAD_OAM',
      meta:{depth:5},
      component: OneAD_OAM
    },
    {
      path: '/OneAD_QP',
      name: 'OneAD_QP',
      meta:{depth:8},
      component: OneAD_QP
    },
    {
      path: '/OneAD_DesignOps',
      name: 'OneAD_DesignOps',
      meta:{depth:9},
      component: OneAD_DesignOps
    },
    {
      path: '/OneAD_ODM',
      name: 'OneAD_ODM',
      meta:{depth:6},
      component: OneAD_ODM
    },
    {
      path: '/LD_HCM',
      name: 'LD_HCM',
      meta:{depth:7},
      component: LD_HCM
    }
  ],
  // 讓換頁後的捲動位置回到頂。詳見 https://router.vuejs.org/guide/advanced/scroll-behavior.html#async-scrolling
  scrollBehavior (to, from, savedPosition) {
    // return desired position
    if (to.hash) {
     
      // 這個是透過 to.hash 的值來找到對應的元素
      // 照你的 html 來看是不用多加處理這樣就可以了
      // 例如你按下 #3 的連結，就會變成 querySelector('#3')，自然會找到 id = 3 的元素
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          selector: to.hash
        }, 0)
      })
    }
    else{
       // 切頁時回到頂，但有等待1000ms
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve({ x: 0, y: 0 })
        }, 50)
      })
    }
  }
})
