			<div class="page-body">
				<h1 id="b4883110-4b85-4370-890f-2e862d7fa16c" class="">簡介&amp;UI成果</h1>
				<figure id="23cb9d42-7ec6-4284-955b-e32954cf2ee1">
					<video class="img-responsive portfolio-item" autoplay="autoplay" loop controls=""><source src="@/assets/img/qp_sample.mp4" type="video/mp4">Your browser does not support the video tag or this video format. You can download the video anyway.</video>
				</figure>
				<p id="f476bf38-a6e8-483f-a31d-f7a54ce2b9de" class="">QP是<mark
						class="highlight-yellow"><strong>集我所有技術和觀念於大成的產品</strong></mark>，</p>
				<p id="15a0362d-2b21-4c71-8f3b-aace5790fd4d" class="">在它發展的過程中，我<mark
						class="highlight-yellow"><strong>同時支援資淺的PM與RD</strong></mark>，</p>
				<p id="716b659f-0026-42b1-b782-4f77b7f4596c" class="">儘管它不是完全合我的心意(需要和其他協作者妥協)，但最能代表我近年的綜合能力。</p>
				<h2 id="12a8125a-5170-4eab-9a2f-2ebb2de95d38" class="">商業邏輯</h2>
				<p id="3ed10371-0d0c-4c35-a8eb-fccab4f33868" class="">OneAD過去透過人工方式(Data Team)，替客戶在下廣告前先估算整波廣告活動的成效。</p>
				<p id="30f20528-8737-4663-9aeb-ae14028ebb6d" class="">為減輕內部人員的作業負擔，並增加客戶下廣告的意願( 更快拿到估算結果、且能自行操作)，決定建立一套<mark
						class="highlight-yellow"><strong>即時的廣告成效預估系統，名為QuickPlan</strong></mark>。</p>
				<h1 id="32d22bde-a07e-4452-a915-f71ea7d8efb6" class="">我的角色</h1>
				<h2 id="57f740a9-bbb8-43c3-a247-b2dc19616a1d" class="">UI/UX Design + Vue切版 + 敏捷式開發提倡者</h2>
				<ol id="4142e2aa-8f88-43af-b00a-12b854705cbd" class="numbered-list" start="1">
					<li>釐清PM需求</li>
				</ol>
				<ol id="66cb202e-308b-47fe-a7be-119793bbdaf9" class="numbered-list" start="2">
					<li>以敏捷式開發的精神，主導EPIC拆分，訂定多階段開發交付的藍圖</li>
				</ol>
				<ol id="cfa976d1-1f01-4957-9348-9813ee2c3cde" class="numbered-list" start="3">
					<li>撰寫一部份Vue.js，幫助工程師團隊完成他們不拿手的精細互動，包含靜態切版與特效js的使用(包含轉場與HighChart)</li>
				</ol>
				<ol id="4b5b1e32-e9c8-4aa3-b364-ff71bd05707a" class="numbered-list" start="4">
					<li>設計，從Wireframe → Prototype → Mockup → HTML/CSS/JS 一路進行產出</li>
				</ol>
				<ol id="e2a90fd2-7540-470d-adf3-2a8620c66f1f" class="numbered-list" start="5">
					<li>說服老闆及PM採用GA來追蹤產品成效，並親自操作GTM來埋設GA Event Tracking、持續整理報告</li>
				</ol>
				<h2 id="f455ffbe-1bfb-4f84-8f0d-87694747d337" class="">克服問題</h2>
				<ol id="8e7e188a-0b5c-45c4-ac2c-caff23964f50" class="numbered-list" start="1">
					<li>曾協助離職潮後全新一批工程師接手此專案的開發，親自Vue.js切版來降低他們上手的難度</li>
				</ol>
				<ol id="48a577d4-931b-40eb-bb59-6b2b753ec1ba" class="numbered-list" start="2">
					<li>協助<strong><mark class="highlight-yellow">對敏捷式開發毫無概念的PM、RD
								Leader</mark></strong>，將此一大型專案劃分成可分配完成的backlog，透過數個月的迭代來確保開發品質、並符合stackholder(老闆)的期望</li>
				</ol>
