				<h2 id="a6e320d4-5217-47c5-9987-e3dbab7f5778" class="">1.0 Waterfall</h2>
				<figure id="c5b84726-bc67-495b-98ca-b8a325a6ac71" class="image"><img style="width:336px"
							src="@/assets/img/QP1_calculate.png" /></figure>
				<p id="10d7c36a-33ea-461b-b87a-de8badf0d0ed" class="">在第一版，我在團隊中的話語權較小。</p>
				<p id="d6b68da8-a076-4319-b651-a061c9851fe7" class="">PM與RD主管採用傳統瀑布式開發，企畫到指定的Layout出來後，才讓設計師與開發團隊加入。</p>
				<p id="96926e6b-6923-41dd-8ba1-067262b7d0a8" class="">PM先和高層主管定調了一個wireframe，同時期望做RWD開發。</p>
				<h3 id="714ce942-3c73-4076-8227-609d847b6352" class="">我的處理</h3>
				<p id="0cb3e560-61d2-4eed-9835-41606fc82a07" class="">因為是一個<mark
						class="highlight-yellow"><strong>全新專案的開發</strong></mark>、且從<mark class="highlight-yellow"><strong>未驗證 Data
							Team 能否給出符合商業需求的計算模型</strong></mark>，</p>
				<p id="2a4865a8-f8b0-40a8-991e-0fda10b5bfec" class="">另一方面，開發出來的工具至少能造福公司內部使用者，我認為儘早完成一版MVP，有助於PM和老闆驗證商業構想。因此我做了：
				</p>
				<ol id="8152e81f-8d4c-4795-a1e8-07cb0dc77f00" class="numbered-list" start="1">
					<li>延用公司在其它工具系統的設計以求快速產出計算頁。</li>
				</ol>
				<ol id="624bc193-2fc5-4018-91ea-206c73f3b5f1" class="numbered-list" start="2">
					<li>勸退PM對RWD的需求，避免在商業概念驗證前，便投入多一倍的開發時間來做RWD – 此處商業概念指得是「此網站對User有吸引力、且有一定比例的手機操作需求」</li>
				</ol>
				<ol id="ebdcdc2a-48f3-409d-895b-834a7b217679" class="numbered-list" start="3">
					<li>在完工後的網站埋GA，紀錄各面頁的使用量</li>
				</ol>
				<h3 id="47468b98-18bc-46b5-9b02-f0c0acde1162" class="">成果</h3>
				<ol id="3064cb11-29ee-4400-b690-a9da840c2ee0" class="numbered-list" start="1">
					<li>Data Team多花了1個月的時間才完成跟產品的銜接</li>
				</ol>
				<ol id="31f9f34e-f11b-48b7-8913-4febbb104785" class="numbered-list" start="2">
					<li>老闆擔心成品太過像專業工具，難以打動外部使用者；因此決定此版本僅給內部人員( PM &amp; Sales )使用</li>
				</ol>
				<ol id="5264ffc2-fa93-4065-8e73-6628fa1c6485" class="numbered-list" start="3">
					<li>老闆不滿意網站成品和期望差太多；PM則自認設計都有給老闆審核過，不知如何改善 ( <mark
							class="highlight-yellow"><strong>也因此給了我推廣敏捷開發的機會</strong></mark> )</li>
				</ol>
				<ol id="7605fad9-da75-4262-b608-668114f5ac40" class="numbered-list" start="4">
					<li>以上證明我的預期是對的 - 先開發MVP，不理想化的預期結果，發現PM與RD團隊的運作不順之處，以在下一次的迭代做出更正確的決策，避免把能量耗費在需求未知的功能(如:RWD)上。</li>
				</ol>
				<h2 id="63c23996-ebe8-4c4f-8dd0-be189ba8669c" class="">2.0 SCRUM ( Partial )</h2>
				<p id="9160fc22-3d99-4b05-87b8-42b1b4bf54f4" class="">
				</p>
				<div id="28205750-a973-482d-89c3-2ff4b046ea07" class="column-list">
					<div id="c5bd300e-94c5-4899-9f5c-1bc01a1b5934" style="width:50%" class="column">
						<figure id="3064b321-9007-4aa9-b7bc-d376c86c7457" class="image"><img style="width:480px"
									src="@/assets/img/QP2_entrance.png" /></figure>
					</div>
					<div id="fd14834d-e6a3-4f43-badf-d04505c04ee8" style="width:50%" class="column">
						<figure id="0109cf6f-46b7-4601-840f-6197f6cd5486" class="image"><img style="width:432px"
									src="@/assets/img/QP2_report.png" /></figure>
						<p id="49ee6b3a-d356-4278-8a9c-c446ee75b6eb" class="">
						</p>
					</div>
				</div>
				<p id="95494a08-8440-46f0-9801-72145d2a77cf" class="">在QP2.0，老闆希望做一組新手教學的入口，幫助外部使用者做簡易版的操作。</p>
				<p id="5d885e93-2470-47b9-b562-74e7cbc72558" class="">同時也<mark
						class="highlight-yellow"><strong>定義外部使用者為廣告Agency的Planner &amp;
							Operator</strong></mark>，但這是基於公司長期對廣告生態圈的理解，而非User Research而來，它可能有誤。</p>
				<p id="2275d4ee-483b-4e00-95b3-4b4df9452c43" class="">儘管如此，我依然缺乏能說服公司做事前User Research的能量，因此選擇先實現一些已說服公司做的改進事項。<mark
						class="highlight-yellow"><strong>其中最重要的就是落實迭代與量測的概念於新一波的開發中</strong></mark>。</p>
				<h3 id="0d074220-cbc5-4a3b-80ff-2e90d2bc504e" class="">我的處理</h3>
				<p id="1b7ed0d5-564f-4b82-9263-2fbd378ca1bc" class="">在這期，得益於公司對前階段的反省與RD主管的離開，我的話語權因此提升。</p>
				<p id="575041ad-e4a2-4d79-8856-d2ff524bca8f" class="">我向開發團隊「分享」了SCRUM的方法；並同時說服老闆與PM，將二期要開發的功能拆成可分批產出的Epics，依序做設計。
				</p>
				<p id="c3d3c407-b068-4543-9651-fda7259b8b87" class="">同時，我參加RD 每日立會，掌握開發進度和我設計進度的時間差，進而掌握好空檔對部份做<mark
						class="highlight-yellow"><strong>親自Vue.js切版</strong></mark>，以向團隊樹立「高質感Web UI」的範例 –
					修正因為公司在過去都只需做對內系統，所以對RD的UI切版品質要求不足。</p>
				<h3 id="ad49fff1-edb3-4a52-b94d-92c7d5222046" class="">推動GA量測，成為觀測產品功能與否的指標</h3>
				<ol id="cca3b524-d0d3-499a-baef-a3fcd31083fc" class="numbered-list" start="1">
					<li>我利用適當機會，向老闆展示QP第一期蒐集到的數據與意義。結合其他說詞(其他公司如何作、我們自己的廣告服務怎麼做)，成功讓老闆要求PM開始在未來所有專 案提供數據指標作為成果</li>
				</ol>
				<ol id="38ff08d1-e220-4db5-9443-229289abd1a2" class="numbered-list" start="2">
					<li>整理我已學會如何用GTM做GA追蹤的項目，分享給PM部門，讓他們能在對「GA能追蹤什麼」有概念的情況下去規畫在QP第二期希望做什麼追蹤</li>
				</ol>
				<ol id="37579f15-2d01-402e-80eb-09f2eead6c13" class="numbered-list" start="3">
					<li>為鼓勵PM勇於嘗試並將數據追蹤做為習慣，我在PM提出希望追蹤的項目後就儘快實踐GTM能做的項目幫忙實作，僅留無法依GTM做的項目待RD開發另外的紀錄方式</li>
				</ol>
				<h3 id="8923ee77-a9cb-4ca4-be3e-eeee4c448457" class="">Retrospective Meeting 培養團隊共識 </h3>
				<p id="ea6b3476-c1a3-4b12-b06f-27ea7962cb15" class="">在專案接近尾聲時，我發起了一次Retrospective meeting，<mark
						class="highlight-yellow"><strong>以Brainstorming的方式鼓勵PM與RD們講述自己在專案期間碰到的困難</strong></mark>，讓大家知道未來可以如何達成更好的配合。
				</p>
				<p id="911947bf-3e89-4c65-8710-ca44f959272a" class="">我也在QP2.0上線後，定期將GA分析出的洞察分享給原開發團隊，增進大家對的認同度與產品IQ。</p>
				<h3 id="a889ffe1-1acb-4a59-aa80-0876f6a75ebe" class="">User Interview</h3>
				<p id="089744be-8a88-4377-b10b-825a6bf87d8f" class="">在QP上線3個月後進行一次User Interview，針對內部的業務User</p>
				<p id="71db37ed-c0bc-4b19-a57f-3f6d771d5c19" class="">並得到了一些有趣的Feedback</p>
				<p id="ba5481fe-dc71-4584-9be8-b630a05c31c4" class="">
				</p>
				<h2 id="c3874d41-4c56-44d5-a359-115f716b536c" class="">3.0 Lean UX</h2>
				<h3 id="8cd5c6bf-1da9-4560-9ccd-b82b55ce8aea" class="">背景</h3>
				<p id="bee6b5c8-a96a-48d7-9e64-c1a1d7863573" class="">在QP2.0上線後，公司花了幾個月的時間宣傳它，讓它成為公司對外的AI形象代表與內部業務爭取預算時的計算工具。</p>
				<p id="3782d99e-7d3d-467f-aa4b-ce3b5e853db9" class="">後來因公司的廣告產品線大調整，所以QP必須順應產品線的賣法變化做&quot;計算頁面&quot;的大改。</p>
				<p id="07f2699d-6711-4ca5-a7ca-1c9b6d0d5e73" class="">我則趁此次Redesign的機會，把在前兩版中蒐集到的問題做一次整理。</p>
				<p id="e4ffbdb1-237a-4c56-b75f-bc16b3d04d87" class="">同時，也進一步導入更多LeanUX的方法，讓PM與開發團隊能做出更精準的決策。</p>
				<h3 id="e976402f-d635-4a3c-badc-9207ead129a2" class="">透過讀書會介紹LeanUX的概念</h3>
				<p id="f6c7e62c-16c5-482a-b78d-30bd4ceadd05" class="">我先後和RD與PM開過LeanUX讀書會，雙方都認同LeanUX的概念很適合解決OneAD某些專案的問題。</p>
				<h3 id="6bf4857a-0a8b-4267-9c61-f0a868e09db8" class="">說服PM加入Daily Scrum</h3>
				<p id="dce91009-fe9f-4d17-bd72-c9b9f6e81350" class="">透過數次溝通，邀QP的專案PM加入RD Daily SCRUM中，並開始體會每日溝通帶來的好處。</p>
				<h3 id="15225fe4-9a44-4fdd-bc5a-af075401208a" class="">問卷調查</h3>
				<p id="cf3dda9e-44bb-4f81-a701-55f3d9fa69a6" class="">發了一次問卷調查</p>
				<p id="80becb54-457e-4424-99b4-82340ec58d03" class="">
				</p>
				<h3 id="23e1c35a-39e2-40d2-b3ed-6b837f30d4f4" class="">Usability Test</h3>
				<p id="ecd4bc40-6135-40e1-8850-8937b3ef464e" class="">設定任務與招募</p>
				<h3 id="26caa727-e4f6-40f0-a83e-066c7fd57955" class="">改良Highchart Prototype方法，令DataTeam可提早確認Data完整性</h3>
				<figure id="5f4a779d-79f9-456c-b0c4-a8f9b59eb80d">
					<div class="source"><a
							href="https://codepen.io/getterbeam/pen/poJomjv">https://codepen.io/getterbeam/pen/poJomjv</a></div>
				</figure>
				<hr id="f9f18f98-acb4-46b9-ac10-18153932892f" />
				<h1 id="3e186a0c-4c32-42e2-9219-fc251fd7cae2" class="">綜合以上3階段，目前的產品成果</h1>
				<hr id="26137e91-cabb-4e82-988b-b261810822c8" />
				<p id="4dfced1f-8ae3-4f9e-8f0f-e7192eb6028a" class="">QP 2.0的使用量穩定上升，</p>
				<p id="ac6344e0-67f3-44b0-8594-d7834330b21b" class="">內部Sales的使用量最大。</p>
				<p id="6b40b0d5-352c-4ff7-b56a-d32d3ad205e0" class="">也透過問卷調查，了解過外部的使用者其實際的使用動機。</p>
				<h2 id="0c77232f-1fa4-44da-8eb0-0687167de980" class="">User是誰 → 輪括完善化</h2>
				<p id="1141dd38-6ce6-4865-8ac1-67e76e1c3716" class="">內部使用者為PM與業務，主要為客戶代為估算廣告成效，次要目的是外出提案時展現火力用。</p>
				<p id="8dd1ae86-24f0-4208-830c-37a85e661c3b" class="">外部使用者為廣告代理商的Planner，在策劃廣告案時會利用此工具了解OneAD可以怎麼鎖定受眾。</p>
				<p id="651688bc-9a20-4556-9b5f-1d345e7493b3" class="">
				</p>
				<h2 id="9c4fcf5a-bc8e-4f39-b2a4-9c40882c3f34" class="">User數量、User</h2>
				<p id="16d35c7c-891e-4cf9-88e1-b9bab27ea236" class="">PM估計外部使用者的人口極限值是500~600人，</p>
				<p id="34b5cbc4-7780-4f7d-bff4-f62e3466da69" class="">隨著QP的上線與多次的宣傳，註冊與登入使用者每月固定上升。</p>
				<figure id="65ce223f-0f04-4976-aa74-58df6eba1aaf" class="image"><img style="width:672px"
							src="@/assets/img/QP2_user.png" /></figure>
				<h2 id="c2f30271-7132-4733-8af9-bddbbe4bb8f0" class="">設計產出</h2>
				<p id="9b66aaf9-5463-4667-9285-59b950bf610a" class="">
				</p>
				<h2 id="9bb5e65d-271e-4486-980a-f96ba9cf8369" class="">建立數據追蹤機制</h2>
				<p id="f2105417-2207-4760-afcc-8b49aea52c4b" class="">．趨勢 by GA ( 快速設定，長期監控趨勢 )</p>
				<p id="0275c22b-f1f3-4610-8d2a-75d6850bbd74" class="">．hotjar ( 快速設定，以個案方式觀察User操作行為 )</p>
				<p id="b54269dc-c6d4-4e71-8925-ac204f891b7b" class="">．計算log by 內部開發 ( 費時久，最詳盡+方便統計)</p>
				<h2 id="c42b8444-ffcc-4096-8f1a-0fe6da4ba821" class="">Usability Test</h2>
				<p id="6617ae90-7a9c-40e9-a63d-7eeae874691f" class="">．新功能上線前，從公司內部找對產品熟悉度不同的內部角色來試用</p>
				<p id="0e15a10d-2809-4fdb-bc16-a930f0d52a28" class="">從而發現設計、開發或數據面上的漏洞，即早進行修正</p>
				<p id="c319e334-01a4-4b42-ad3f-1a6e7e632183" class="">
				</p>
				<h1 id="f8949b99-c981-406a-947b-906ff893df29" class="">心得</h1>
				<p id="f0e9c1db-e0d0-43c6-8fd6-bba9fffef4bd" class="">透過幾次的內部訪談，我可以認為：QuckPlan產品的核心價值是</p>
				<ol id="b21fc43a-40a3-4166-8b49-21b9616f4089" class="numbered-list" start="1">
					<li>量體計算自動化</li>
				</ol>
				<ol id="396f7057-cc1b-48ab-9782-d60455d0ae01" class="numbered-list" start="2">
					<li>受眾輪括圖表繪製能力</li>
				</ol>
				<p id="a9ba4359-52c3-4c79-9979-3f930d6f410f" class="">未來在使用者獲取上，可以透過行銷部門宣傳此2點；</p>
				<p id="b2ea7eca-b2b3-457b-8475-a5e4b036e7a1" class="">在功能上也應著重優化這兩塊。</p>
				<!-- <h2 id="0ef79b26-4a88-4557-8522-4e93324e02ef" class="">說服一個瀑布式開發的組織改採迭代開發的經驗</h2> -->
				<p id="7c706c6b-7276-4e80-ae16-a640a7e80b5f" class="">
				</p>
				<p id="ff6b935e-deb1-4198-a880-2c3128fb36a4" class="">
				</p>
			</div>
		</section>
	</div>
