<template>
  <div>
	<Nav mainTitle="OneAD設計團隊營運經驗"/>
	<section class="article_container">
			
		<div class="page-body">
			<p id="e0f07d30-715e-45a3-8a6f-73cc16614867" class></p>
			<p id="3b97e064-6406-4c27-8cb1-0e818c3d282b" class></p>
			<figure id="71b7707e-27c0-4423-8652-1844bbc9cd57" class="image">
				<img
				style="width:"
				src="@/assets/img/DesignOps/Cover.png"
				/>
			</figure>
			<h1 id="f0d1b161-f4e1-4559-95a5-c35bbf82f6be" class>背景</h1>
			<p id="10353914-7750-4b94-83c3-10423b74734e" class>2020年1月，我接到任務，開始管理公司的2名視覺設計師，獨立成為一個設計部門。</p>
			<p id="24fe5b88-16ed-4de8-9c05-51bfdec9b639" class>此前視覺設計師們是在廣告客服部門為各種廣告專案做素材設計，同時行銷做形象設計。</p>
			<p id="3ece2f92-4c76-41c9-9479-b5279305c65b" class>雖然我們的主責任務不同，( 我負責設計網站與廣告格式的UI/UX)</p>
			<p id="802c4c56-aa13-4342-82f1-6cde3534ec4e" class>但公司期待我能透過自己的UI/UX know-how來帶動他們的生產力提升，</p>
			<p id="07fa180f-a229-408c-8795-41c1605e2433" class>
			<strong>對我而言也是個實證自己部門經營理念的機會</strong>，因此我在接手半年內陸續做了許多Design Ops的措施，如果我未來有機會帶領UI/UX設計師，也將延續這樣的思路。
			</p>
			<p id="89e53d27-4eee-48d4-83e3-cd4637ffc761" class></p>
			<h1 id="eee4883d-8a7b-4197-869c-72c2b23f2442" class>公司給我的目標</h1>
			<p id="8c7585a2-8e6f-4b2c-a6b5-41eceec35a28" class></p>
			<figure id="e87b2ff8-580f-4bd7-a0da-4d8f19db415b" class="image">
				<img
				style="width:626px"
				src="@/assets/img/DesignOps/goal.png"
				/>
			</figure>
			<h2 id="a20ce95e-74d4-4567-9120-c540be7161a8" class>1.提升視覺設計師的表現</h2>
			<h2 id="00fdd2d7-7967-46a0-b67e-e08d275e453b" class>2.教導視覺設計師UI/UX技能，做為留才手段</h2>
			<h1 id="b7f8006b-a398-4eb2-b320-37d50b7d7e57" class>Design Ops的想法</h1>
			<p id="9d1d6bc0-e677-44b5-9d9a-cc22935b4c65" class>
			參考：
			<a
				href="https://www.nngroup.com/articles/design-operations-101/"
			>https://www.nngroup.com/articles/design-operations-101/</a>
			</p>
			<p
			id="f265129b-8355-4b8c-97d7-3ef93fd7d46e"
			class
			>我將我所經營的設計部門視為一個系統，而這個系統要如何持續優化，以讓它的內部User(我的2位視覺設計成員)與外部User(需求方)都能有好的體驗，以致我們能有好的產出和滿意度？</p>
			<p
			id="6b8bd4ca-b0a4-4078-b998-18d11c04accb"
			class
			>正如設計一款產品一樣，我同時從質性(短期、快速訪談)與量化(長期紀錄、觀察趨勢)的方向下去掌握部門的狀況，以下便是我的做法。</p>
			<h1 id="ee2c4212-7eb9-4e67-aa0a-26b43a801482" class>我的實際做法</h1>
			<figure id="e3ca31aa-37a6-4227-89cb-56626aca43a2" class="image">
				<img
				style="width:432px"
				src="@/assets/img/DesignOps/plan.png"
				/>
			</figure>
			<h2 id="35532773-0287-41b4-8c31-20491bdfe9ec" class>1.留才</h2>
			<p id="de895db8-4bc6-4879-b7e5-0fb7c990b394" class>身為一個空降主管，我最想避免的是避免短期內的人員流失，</p>
			<p id="61db03cc-3e31-4e70-b2e0-5448cd49685f" class>因為視覺設計師們負責的製作項目種類繁多，和其他部門也有既有的合作生態，</p>
			<p id="bb641881-07a2-4955-9125-8747eafc813f" class>若突然出現人員流失的話，我連接手他們的工作量都可能無法上手、</p>
			<p id="3cea8bd0-311d-4604-9455-b758c39de9cb" class>更會延遲我的主線設計任務與部門營運計畫。</p>
			<p id="b207af0c-b438-4028-b144-7a5c61611323" class>為此，我在確定空降後，火速約談了2位設計師與他們的前主管，</p>
			<p id="6f40ede8-ad3f-4bdc-986f-eb47ac0d66f5" class>了解他們期待的學習方向和期待新部門能給予他們的幫助。</p>
			<p id="9d5611df-cf24-4c82-9e1f-3a0d87aa1304" class></p>
			<h2 id="5d5ac76d-e008-4b25-b85a-24da6418c4e8" class>2.建立溝通管道</h2>
			<ol id="fa150247-718d-40e4-b159-b71e20ad6fd3" class="numbered-list" start="1">
			<li>要依SCRUM的型式進行每日進度立會，讓成員知道Team Leader的我在做什麼UI/UX項目，同時我也可以逐步理解他們的日常工作組成、還有常碰到的問題</li>
			</ol>
			<ol id="eefc9ed3-cda7-45e8-97bd-1aceecdd8db9" class="numbered-list" start="2">
			<li>
				每週部門分享會，要求設計師們每週一次會議分享自己在工作或日常學習中得到的心得，
				<mark class="highlight-yellow_background">
				<strong>我透過這樣的機會理解他們的設計方法，也藉機分享一些設計思考或UI/UX技能給他們</strong>
				</mark>
			</li>
			</ol>
			<h2 id="22926770-ae2c-40eb-9bb5-86c467944524" class>3.理解成員的思維與能力</h2>
			<ol id="2975f901-70be-49bf-90c0-a7c5b2ca9c7f" class="numbered-list" start="1">
			<li>在日常的既有工作，我避免太過介入、強硬變更他們已習慣的做法；</li>
			</ol>
			<ol id="f13a4324-836b-4331-906c-950df360bb80" class="numbered-list" start="2">
			<li>而是透過每日立會發現特定工作漸漸出現問題，例如被需求方退回多次、或設計工作卡關較久，則此時我能夠名正言順的介入了解。</li>
			</ol>
			<ol id="43fddaa5-0bfd-4c40-b99a-903df42f78d0" class="numbered-list" start="3">
			<li>經過幾次介入指導卡關的設計工作，並主動發給他們的小型UI設計Project，已能掌握2位成員的思維與能力，了解他們的優缺點。</li>
			</ol>
			<h2 id="1b904a15-f73f-417b-9b01-79cd62e07d1b" class>4.優化成員的做事方法與資源</h2>
			<ol id="8d3bc33f-4d14-40a9-af69-e5989e4a2787" class="numbered-list" start="1">
			<li>我會在日常的討論中一次次說明、示範、和提醒成員們更有效率的做事方法，透過事實去「說服」他們，而非只是口語上的說「我認為應該怎麼做比較好」，因為他們在我空降之前已有自己的一套方法，逼急了可能會有反效果。</li>
			</ol>
			<ol id="1ccdc2d6-59c2-4d05-9fc7-afdf799f184b" class="numbered-list" start="2">
			<li>
				我以季為單位，向成員訪談他們工作上缺少的資源或SOP，並依緊急程度漸進式的改善。例如：
				<ol id="098e737d-91a4-43d3-b12a-b28c9f4a281e" class="numbered-list" start="1">
				<li>商用圖庫準備</li>
				</ol>
				<ol id="9f1e1cd8-fb02-407b-9134-f0e90ffc4515" class="numbered-list" start="2">
				<li>廣告設計工作SOP制定</li>
				</ol>
				<ol id="173244ff-9775-4070-b3c5-be53b20af277" class="numbered-list" start="3">
				<li>AE PlugIn請購</li>
				</ol>
			</li>
			</ol>
			<h2 id="95b99a59-0396-4c68-801b-d85bf5408500" class>5.量化部門成員表現</h2>
			<p
			id="65dca1f0-1b4b-40d7-8bd1-8fb7c2fe7915"
			class
			>OneAD傳統上沒有記錄、預估設計部門能量的方法，所以這是我自發性為了在未來主動提出補人需求、或證明我有提升視覺設計效能的依據。</p>
			<p
			id="2d6e906a-6353-4a05-99d8-8862d0b9687d"
			class
			>我要求部門成員每日持續將工作項目與狀態更新於雲端系統中(Monday.com)，並因此可以做趨勢圖表觀察。</p>
			<p id="df670dcc-3969-4249-84ed-cfd5fc0673a5" class>這樣的量化資料也能驅動成員們有方向的進步。</p>
			<h2 id="56e15afa-7d22-4ee7-b20d-01a4a37af259" class>6.部門知識文件化</h2>
			<p
			id="af3a7706-7553-45d7-8319-9ac3bbd0a197"
			class
			>這件事是尚在初步進行的階段，我認為包括我的工作在內，應該將相關文件做成一種Wiki，以便在需要時(有人緊急請假或離職)時，公司能快速掌握狀況並接手，同時筆記化的這件事本身就並設計師們有幫助。</p>
			<p
			id="8d258c63-5f74-4dee-a098-485508cef52f"
			class
			>這個Case算是是重要但非緊急的事，我打算漸進式的累積，在整體有個眉目時再用來要求部門成員參與編輯。</p>
			<p id="75458a96-ca9f-40b1-9c4b-fd5b480cc5c8" class></p>
			<h1 id="26415883-22ad-4377-b8ec-d21664d2a3bb" class>挑戰</h1>
			<figure id="ef2d18ca-70bd-4d6b-9f94-211809eb2e64" class="image">
				<img
				style="width:626px"
				src="@/assets/img/DesignOps/difficult.png"
				/>
			</figure>
			<h2 id="77e3aae3-1a30-4a1c-86ed-86de2ccc45e5" class>成員會抗拒變更行之有年的方法</h2>
			<p
			id="f82ccafa-9b82-498f-8dc4-930c513f6f3c"
			class
			>既有的設計成員中有一位資深者已在公司待了2年，現有的視覺設計SOP可以說是配合他的工作習慣與設計方法而特化過。這會造成2個方案的影響：</p>
			<ol id="6fb3efd2-9ef5-40ac-b25d-d428330b1408" class="numbered-list" start="1">
			<li>我或需求方認為他有待加強的表現，他可能認為自己已經做得夠好</li>
			</ol>
			<ol id="50bf18e7-a753-4257-bfc8-5bdaee361e4e" class="numbered-list" start="2">
			<li>有些可爭取、可協調的事情( 例如：急件、過多次改稿、抓不清老闆的視覺喜好)，他會認為是無法改變的&quot;必要之惡”，而不去改變現況、也不認為是可以提出來請我設法改善的</li>
			</ol>
			<p
			id="df001537-0337-41fd-9a79-3233fbbcdb35"
			class
			>這兩點我需要透過較長時間抓出脈絡來，或等問題放大到造成設計師們深感挫折時，才較能說服他們配合我做方法調整。</p>
			<h2 id="7321084f-59af-44ef-8200-3d989de0c4d3" class>上層主管們的錯誤期待</h2>
			<p
			id="8f333a4b-831f-4cc5-a470-67a88e8e18ad"
			class
			>老闆和我的主管們最初期待：我可以在維持視覺設計師們的既有工作的同時教他們UI/UX技能，以分攤或是接手我的UI/UX工作。</p>
			<p id="1b86a277-d341-4c62-8f6c-db9f85423145" class>但這事有三個前提：</p>
			<ol id="774eb422-cc54-417a-892b-b3cad03d5679" class="numbered-list" start="1">
			<li>視覺設計師在既有工作之外有足夠的時間進行UI/UX設計</li>
			</ol>
			<ol id="0f6da592-619d-469f-b5f9-66d0f9d5bd3b" class="numbered-list" start="2">
			<li>他們有主動學習的意願</li>
			</ol>
			<ol id="dc1137bd-b078-466d-ab07-de990b0d70f3" class="numbered-list" start="3">
			<li>有對UI/UX專案的分析能力</li>
			</ol>
			<p id="77946706-5395-4699-b3a4-00a6bb6d0d87" class>這方面的條件或多或少無法滿足，我也輾轉的向主管們證明了此事。</p>
			<h2 id="a02e2464-0101-476b-8cb5-2d8dd4f76635" class>人員數少，對人員流動的抵抗力差</h2>
			<p
			id="917a7b07-4e42-4d02-b159-1b3736c623ae"
			class
			>只有2人的成員，特別1個是資深、另一個還來不到1年，就算部門的知識庫建立完成，我相信只要有一人離職，還是會造成另一人的龐大壓力。</p>
			<p
			id="b1c8f6c0-6191-46f9-b7ef-9965a7f1e00b"
			class
			>所以持續紀錄量化的部門工作狀況，以便說服公司再給我head count，是我未來半年的努力方向。</p>
			<p id="c71141f4-9c16-42cb-bc0c-a8e60f9d23c5" class></p>
			<figure id="4791bbea-c252-4f82-8da8-cf6fefc16e5f" class="image">
				<img
				style="width:626px"
				src="@/assets/img/DesignOps/moveon.png"
				/>
			</figure>
			<p id="2a2d4e59-e030-4f0a-8836-3bbb9cee79a0" class></p>
		</div>
	
	</section>
  </div>
