<template>
	<div>
		<Nav mainTitle="Redesign for 'Mos Order' APP"/>
			<section class="article_container">
				<div id="article_requirment">
					<div class="p-title">
						緣起
					</div>
					<div class="">
						本專案是自發性的練習，目的是針對我在實務中較無經驗的面向 ( B2C & 手機應用 ) 做發揮。
										<br/>
										主題為摩斯漢堡的點餐APP，使用者可以透過它在指定門市點餐後到店快速取餐。
										由於我曾大量使用，觀察到了其中許多值得優化的使用經驗。
										<br/>
										以此為出發點，進行了一系列的Redesign。
					</div>
					<div class="p-title">
						痛點分析
					</div>
					<div class="p-title-sub">
						首頁主線不明顯
					</div>
					<div class="row" style="">
						
						<div class="col s4 offset-s1" style="text-align:right;">
							<img src="@/assets/img/Works/05-01-index.jpg" alt="" style="text-align:right;width:auto;height:420px;"/>
							
						</div>
						<div class="col s5"  style="text-align:left;">
							<ul>
								<li>
									點餐功能為 User 主線，但在畫面中太多干擾使用者第一時間進入點餐的支線。同層級的分叉路口有5個，Banner更是最搶眼的元素，偏偏Banner與點餐功能完全沒有連動。
								</li>
								<li>
									「紀錄」可讓User查詢已點餐點或過去已完成的訂單；「分店」則可讓User詢找方便取餐的分店。兩者都與「點餐」功能相關，可能在點餐前後使用，卻分散放在畫面各角落，使用者很能認知到其相關性、進而善用。	
								</li>
								<li>
									「摩斯卡」為「會員」之中的一項功能，卻在首頁中重覆出現，使畫面與動線顯示複雜。	
								</li>
								<li>
									Banner的張數過多，使用者不少會願意等它輪播完、或一張張翻完。且此處多數Banner沒有點擊後的連結，只是靜態展示，也與點餐功能沒有連動。 適合調整至單一頁面，垂直攤平來觀看。
								</li>
							</ul>
						</div><div class="col s2" style="">
							
						</div>
					</div>
					
					
					<div class="p-title-sub">
						訂餐流程容易造成使用者困擾甚至放棄訂餐
					</div>
					
					<div class="row">
						<div class="col s10 offset-s1" style="text-align:right;">
							<img src="@/assets/img/Works/05-01-orderflow.png" alt="" style=""/>
						</div>
					</div>
					
					<div class="p-title-sub">
						相似頁面的一致性低
					</div>
					
					<div class="row">
						<div class="col s10 offset-s1" style="text-align:right;">
							<img src="@/assets/img/Works/05-01-pages_consistency.png" alt="" style=""/>
						</div>
					</div>
					<div class="p-title-sub">
						表單類問題
					</div>
					
					<div class="">
					
						<ul>
							<li>
								會員註冊的表單中，「性別」欄位在點擊時會下拉展開，但使用者為了跳下一個選項而點擊「生日」時，由於「性別」欄位的收合，導致「生日」欄位大位移離開使用者視線，這是不好的互動。
							</li>
							<li>
								其他需展開螢幕鍵盤的欄位，UI上缺乏收合選項 ( OK鈕 ) 或「跳下一欄」的按鈕，使用者只能點擊灰色背景區域才能退回完整的表單檢視，這需要在UI上做加強、或增加鍵盤收合的時機，如 ① 使用者大幅上下滑動螢幕時 ② 使用者點擊目前填寫中的螢幕時。
							</li>
							<li>
								電子信箱被要求填寫兩次做確認是麻煩的事，使用者被迫多keyin一次15個字元以上含符號的字串；密碼的部分，近年來的UX思潮也逐漸傾向免除User的再次填寫
							</li>
						</ul>
					</div>
					<div class="row">
						<div class="col s10 offset-s1" style="text-align:right;">
							
							<img src="@/assets/img/Works/05-01-form.png" alt="" style=""/>
						</div>
					</div>
					<div class="p-title-sub">
						各Selector中的UI一致性低
					</div>
					<div class="row">
						<div class="col s8 offset-s2" style="text-align:right;">
							<img src="@/assets/img/Works/05-01-Selector_low_consistency.png" alt="" style=""/>
						</div>
					</div>
					
					


					<div class="p-title">
						Redesign
					</div>
					<div class="p-title-sub">
						選擇重點功能挑戰UI設計
					</div>
					<ul>
						<li>
							因為本專案的目的在於表現我的設計美感與對 Native APP 設計規範的理解，所以我會略過User Research與Wireframe, Low-fidelity prototype的驗證，直接執行UI設計與動態轉場設計
						</li>
						<li>
							設計的範圍則選擇首頁與訂餐主流程
						</li>
					</ul>

					<div class="p-title-sub">
						訂餐流程 UI Flow
					</div>
					<ul>
						<li>
							以下是我進行Redesign的成果
						</li>
						<li>
							與原本流程相比，我在訂餐頁面各階段加入快速展開「修改分店&取餐方式」之彈窗。使用者可以在任何階段修改分店，並即時了解欲購買的餐店在該分店有無，而不須如舊版那樣，只能放棄點餐進度，跨越多步驟回到最初才可修改
						</li>
						<li>
							同時，我簡化首頁資訊，將Banner與其他非關訂餐主線之功能縮小至底層Tab。突顯點餐主路徑&訂單查詢次路徑。使用者將能被清楚導引
						
						</li>
						<li>
							此外，我改善了各個頁面的Usability。包含重新設計表單以簡化使用者的操作複雜度、減少所須點擊次數；並改善餐點相關Layout，讓美食圖片極大化，讓鮮明的圖片而非舊版的文字吸引使用者的購買欲。
						</li>
					</ul>
					
					<div class="row">
						<div class="col s10 offset-s1" style="text-align:right;">
							
							<img src="@/assets/img/Works/05-02-Redesign_Order_flow.jpg" alt="" style=""/>
						</div>
					</div>

			
					<div class="p-title">
						驗證假設
					</div>
					<ul>
						我需要驗證我的Redesign方案，是否真能更有效率的幫助使用者完成點餐，<br />
						同時也希望進一步了解：
						<br />
						對使用者而言，一個Usability良好的點餐APP是否滿足他們的需求，促使他們安裝與定期回訪的主要誘因？
						<br />
						因此我將edesign的點擊操作做成Prototype，進行了一次User Interview及Usability Test。
					</ul>
					<div class="p-title-sub">
						Usaer Interview
					</div>
					<ul>
						我從生活圈中抽了5人進行訪談，組成為3男2女，都為25~35歲年齡層。主要目的是探索在對新應用接受度最高的年輕人中，會使用速食點餐APP的人佔多少，理由和情境為何？
					</ul>
					<div class="row">
						<div class="col s8 offset-s2" style="text-align:right;">
							<table class="responsive-table striped" style="font-size:12px;" >
								<thead>
									<tr>
											<th>問題</th>
											<th>受訪者1</th>
											<th>受訪者2</th>
											<th>受訪者3</th>
											<th>受訪者4</th>
											<th>受訪者5</th>
									</tr>
								</thead>

								<tbody>
									<tr>
										<td class="interview_question">1.請問您過去半年每月吃速食的頻率？<br>(如Mc, KFC, MOS, 披薩等...)
										</td>

										<td class="interview_answer">4次
										</td>
										<td class="interview_answer">1次</td>
										<td class="interview_answer">4次</td>
										<td class="interview_answer">2次</td>
										<td class="interview_answer">3次</td>
									</tr>
									<tr>
										<td class="interview_question">2.是否曾使用非臨櫃方式點餐？<br>( Web / APP / 自助點餐機... )
										</td>
										<td class="interview_answer">有</td>
										<td class="interview_answer">有</td>
										<td class="interview_answer">有</td>
										<td class="interview_answer">有</td>
										<td class="interview_answer">有, 但是電話點餐 & 得來速</td>
									</tr>
									<tr>
										<td class="interview_question">3.其中，是否有使用過速食店的APP點餐？ <br>過去半年使用幾次？ </td>
										<td class="interview_answer">有, 5~6次</td>
										<td class="interview_answer">無, 常用Uber eat，但未使用單一店家的APP</td>
										<td class="interview_answer">有,但是pizza hot。 2次</td>
										<td class="interview_answer">無</td>
										<td class="interview_answer">無</td>
									</tr>
									<tr>
										<td class="interview_question">4-A. 若有，使用的原因？ </td>
										<td class="interview_question">不急時使用，主要為了外送功能</td>
										<td class="interview_question">累計點數、看優惠。偏好現場取餐</td>
										<td class="interview_question">-</td>
										<td class="interview_question">-</td>
										<td class="interview_question">-</td>
									</tr>
									<tr>
										<td class="interview_question">4-B. 若無，沒使用的原因？</td>
										<td class="interview_question">-</td>
										<td class="interview_question">用Uber Eat也是為了外送，但因是第三方APP，所以餐點圖片不足。且10點後才能接單，不能點早餐</td>
										<td class="interview_question">不喜用APP(要裝軟體)
																									但會為以下原因用Web：
										用餐時段免排隊→最大理由</td>
										<td class="interview_question">大量點餐(5人以上外送或到店聚餐)，會用電話點餐</td>
										<td class="interview_question">-</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
					
					
					<div class="p-title-sub" style="margin-top:40px;">
						Usablilty Test
					</div>

					<ul>
						<li>
							任務一：請受測者使用原版Mos APP，從首頁進入點餐，經過完整購物車流程。並在送出訂單最後一步前要求他們修改取餐分店與取餐方式
						</li>
						<li>
							任務二：請受測者使用我redesign版本之Prototype，並在送出訂單最後一步前要求他們修改取餐分店與取餐方式
						</li>
						透過以上兩個任務，驗證使用者在點餐過程中可能遭遇的困擾，以及我設計的版本是否能幫助使用者找到、並靈活的使用修改分店的功能？
						<br>
					</ul>
					<div class="row">
						<div class="col s12">
							<a class="waves-effect waves-light btn" target="_blank" href="https://w9wkly.axshare.com/#g=1&p=%E5%8F%96%E9%A4%90%E8%A8%AD%E5%AE%9A___%E4%BB%A5%E5%BD%88%E7%AA%97%E4%BB%A3%E6%9B%BF&c=1"><i class="material-icons left"> </i>測試用Prototype</a>
						</div>
					</div>
					<ul></ul>
					
					<div class="row">
						<div class="col s8 offset-s2" style="text-align:right;">
							<table class="responsive-table striped" style="font-size:12px;" >
								<thead>
									<tr>
											<th>任務</th>
											<th>受訪者1</th>
											<th>受訪者2</th>
											<th>受訪者3</th>
											<th>受訪者4</th>
											<th>受訪者5</th>
									</tr>
								</thead>

								<tbody>
									<tr>
										<td class="usability_mission">任務一
										</td>

										<td class="usability_result">
											1.猶豫一下後找到點餐鈕 <br>
											2.想像不出如何改取餐方式
										</td>
										<td class="usability_result">
											1.也覺得改分店要放棄進度，很麻煩 <br>
											2.點餐換餐點，不順手
										</td>
										<td class="usability_result">
											1.餐點觸控區域不理想 <br>
											2.找不到修改取餐方式的功能
										</td>
										<td class="usability_result">1.知道得要退回最初，放棄點餐結果才能改取餐方式</td>
										<td class="usability_result">1.點餐時切換不順，會誤以為某處可點擊，但其實沒有
											<br>
											2.不知道如何修改取餐方式
										</td>
									</tr>
									<tr>
										<td class="usability_mission">任務二</td>
										<td class="usability_result">1.馬上找到修改方式，直覺認為分店的修改是按一整條文字塊而非右側按鈕</td>
										<td class="usability_result">1.找得到分店資訊2.但覺得預計取餐不用放在那邊擾人</td>
										<td class="usability_result">1.直接性的找到修改方式</td>
										<td class="usability_result">1.找得到，但受測者認為是因為知道任務，所以才會特別注意頁面中的改分店功能，以至於很快找到。</td>
										<td class="usability_result">1.稍微猶豫一下即找到修改鈕</td>
									</tr>
									
								</tbody>
							</table>
						</div>
					</div>
					<div class="p-title-sub">
						心得
					</div>
					<ul>
						<li>User Interview 讓我發現光是同年齡層的使用者，對速食非臨櫃的點餐使用與否，就有分歧很大的考量。如果要將更多年齡層考慮進去，將會更有差別。這似乎意味著APP點餐的使用者可能佔速食業者的營收不例不高？ 或許這是一個增加營收(吸引不想排隊的人)或減輕櫃台壓力的解決方案，但它也值得更多的推廣。</li>
						<li>
							點數卡功能或定期促銷，能吸引使用者長期安裝此APP
						</li>
						<li>
							受測者全都在原產品的操作上為修改分店 = 退回最前且放棄訂餐進度所苦，如同我的預期；同時redesign的prototype能讓所有的受測者都能便捷使用。證明了我的設計方向正確，可以順著繼續下去。
						</li>
						<li>在usability test時發現使用者們對點餐點時常會有預期按了有回饋的部分，卻沒有該處的感應區域，這可以在redesign中優化此問題。</li>
					</ul>
					
					
					

					<div class="p-title">
						持續迭代
					</div>	

					<div>
						在確認取餐方式的redesign確實能帶來良好效果後，我以Protopie進行Hi-Fi prototype的製作，以展現我完整的想像，並安排進一步的Usability Test，觀察使用者在擬真模型上，更完整的互動細節，驗證我的想法。
					</div>

					<div class="p-title-sub">
						Hi-Fi Prototype
					</div>	
					<video width="200" height="" controls>
						<source src="@/assets/mos_transition_flow.mp4" type="video/mp4">
					</video>
				</div>	
			</section>
	</div>

