<template>

  <div id="container">
    來接threejs-editor的匯出結果
	
  </div>

  
</template>

<script>
import Nav from '../components/Navbar_index';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';	
// import OrbitControls from 'three/examples/js/controls/OrbitControls.js';
import OrbitControls from 'three-orbitcontrols';
// var controls = new OrbitControls(camera);



export default {
  name: 'ThreeTest',
  data() {
    return {
	  // 這裡在做的事比較像歸0 ?
	  cube: null,
	  line: null,
	  // 以下三者為必要
      renderer: null,
      scene: null,
	  camera: null,
	  controls:null,
    }
  },
  methods: {
    init: function() {
	  	this.scene = new THREE.Scene();
      	this.camera = new THREE.PerspectiveCamera(
        	50, window.innerWidth / window.innerHeight, 1, 500
	  	);
		this.camera.position.set( 2, 12,-18 );
		this.camera.lookAt( 0, 10, 0 );

		this.renderer = new THREE.WebGLRenderer();
		//   this.renderer.setSize(window.innerWidth, window.innerHeight);
		this.renderer.setSize(900, 600);
		// 畫圖前先清空內容
		document.getElementById("container").innerHTML = '';
		// 利用上面宣告的renderer來畫圖
		document.getElementById("container").appendChild(this.renderer.domElement);

		
		this.controls = new OrbitControls(this.camera, this.renderer.domElement);
		
		this.camera.position.set( 2, 12,-18 );
		this.camera.lookAt( 0, 10, 0 );
		
		// this.controls.update();

		// 用以下4行建個物件並加入
		// geometry是尺寸？
		var geometry = new THREE.BoxGeometry(2, 1, 1);
		var material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
		this.cube = new THREE.Mesh(geometry, material);
		// 默认情况下，当我们调用scene.add()的时候，物体将会被添加到(0,0,0)坐标
		this.scene.add(this.cube);
		
	  
	 	// var material = new THREE.LineBasicMaterial( { color: 0x00ff00 } );
      	// var points = [];
		// points.push( new THREE.Vector3( - 10, 0, 0 ) );
		// points.push( new THREE.Vector3( 0, 10, 0 ) );
		// points.push( new THREE.Vector3( 10, 0, 0 ) );

		// var geometry = new THREE.BufferGeometry().setFromPoints( points );
		// this.line = new THREE.Line( geometry, material );
		// this.scene.add( this.line );

		 /*
        添加光源
		*/
		//点光源
		var point = new THREE.PointLight(0xffffff);
		point.position.set(400, 200, 300); //点光源位置
		this.scene.add(point); //点光源添加到场景中
		//环境光
		var ambient = new THREE.AmbientLight(0x999999);
		this.scene.add(ambient);


		let loader = new GLTFLoader();
		let self= this;
		loader.load('/gundam.gltf', 
			function (obj) {
				obj.scene.position.y = 0;
				self.scene.add(obj.scene);
			}, function (xhr) {
				console.log((xhr.loaded / xhr.total * 100) + '% loade')
			}, function (error) {
				console.error(error, 'load error!')
			}
		)
		// loader.load( '/gundam.gltf', function ( gltf ) {
		// loader.load( '/bread.glb', function ( gltf ) {

			// self.add( gltf.self );

		// });
		
      this.camera.position.z = 10;

      var animate = function() {};
    },
    animate: function() {
      requestAnimationFrame(this.animate)

      this.cube.rotation.x += 0.01
      this.cube.rotation.y += 0.01
	  
	  //  這行是必須的，才會開始RENDER
      this.renderer.render(this.scene, this.camera)
    }
  },
  mounted() {
    this.init()
    this.animate()
  }
}
</script>
<style scoped>
  #container {
    height: 400px;
  }
</style>