c('br'),_vm._v("\n\t\t\t\t如果頁面中只有小部分UI為高複雜互動，我會使用Axure來製作單純的部分，再用Axure的iframe block來連結手寫的網頁。\n\t\t\t")]),_c('div',{staticClass:"mindset_note"},[_c('h4',{staticClass:"pri-text"},[_vm._v("User Research的時機與方法")]),_vm._v("\n\t\t\t\t\n\t\t\t\t我目前認知到的軟體User Experience可分為：①使用者需求的滿足 ②UI的Usability \n\t\t\t\t"),_c('br'),_vm._v("\n\t\t\t\t兩者的重要性，在不同的產品上會有相異的比重 -- "),_c('br'),_vm._v("\n\t\t\t\t對B2C軟體而言，由於需求的功能較少，不易和競品做出差異，所以Usability的優劣是一大重點；"),_c('br'),_vm._v("\n\t\t\t\t而對B2B軟體來說，需求功能往往是多樣、且有多種使用者以不同的權限在操作，光是使用者需求的滿足上，就足以拉出差距。\n\t\t\t\t"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t身為一個UX Designer，需要兼顧兩者。"),_c('br'),_vm._v("\n\t\t\t\t在傳統瀑布式的設計開發(UCD)中，使用者需求的調查在於設計前期；"),_c('br'),_vm._v("\n\t\t\t\t而設計出來的UI usability則在設計完成~開發完成中間測試。"),_c('br'),_vm._v("\n\t\t\t\t至於使用者們的真實回饋則有賴Release後的數據、滿意度蒐集。"),_c('br'),_vm._v("\n\t\t\t\t當碰上敏捷式開發，如何以多次迭代配合使用者回饋來調整UX方法，則需要更靈活的變通。"),_c('br'),_c('br'),_vm._v("\n\t\t\t\t\n\t\t\t\t綜合前述，在什麼樣的設計、開發階段，適用的研究方法，我的理解如下：\n\t\t\t")]),_c('span',{staticClass:"img-note center-align"},[_vm._v("Summary from my learning")])]),_c('ul',{staticClass:"resume_content center-align"},[_c('img',{staticStyle:{"max-height":"80vh","width":"auto","max-width":"100%"},attrs:{"src":require("../assets/img/Resume/UX-process-byGetter.jpg"),"alt":""}})])])])}]

