<template>
	<div>
    <Nav mainTitle="Orca HCM RWD &amp; Design System"/>
		<section class="article_container">
			<div id="article_requirement">
				<div class="p-title">
					Requirement Analysis
				</div>
				<div class="p-title-sub">
					Hypothesis(Original Requirement)
				</div>
				<ul>
					<li>
						HCM(LearningDigital's main product, a B2B intranet HRD System) was asked to be able to support on the mobile device for more than three years. However, both it and its competitors hadn't completed a successful improvement in this case. 
					</li>
					<li>
						HCM used to have an old-school UI style originally design for lower resolution(1024px) and ie7 browser, its performance is bad on the modern browser and the display device.
					</li>
					<li>
						The company didn't have any front-end engineer, so the back-end engineers have to build the website by themselves. To help them make better UI with RWD, we designers then planned to build an RWD design system.
					</li>
				</ul>
				<div class="row">
					<div class="col s12">
						<span class="img-note">Old-school UI of HCM which cannot perform well at cross-browser and cross-device</span>
					</div>
					<div class="col s6">
						<img src="@/assets/img/Works/hcm-original-1.jpg" alt="" />
						
					</div>
					<div class="col s6">
						<img src="@/assets/img/Works/hcm-original-2.jpg" alt="" />
					</div>
				</div>
				<div class="p-title-sub">
					External Users ( Our Clients Users )
				</div>
				<ul>
					<li>
						Enterprise HR 
						<span class="detail-description">
							- Main administrator of HCM product. 
						</span>
						<span class="detail-description">
							- Setting most of the HR function for their whole company.
						</span>
						<span class="detail-description pri-text">
							- The key person who decide to buy HCM. 
						</span>
					</li>
					<li>
						Enterprise employee
						<span class="detail-description">
							- Main User of HCM but less influence on which brand of HCM system their company chose.
						</span>
						<span class="detail-description">
							- The reason why enterprises ask for a cross-device HCM product.
							<br />
							- Because some industrial like financial or hotel don't need their employee to sit in the office room but bring a mobile device when they in the work environment.
							<br />
							<span class="pri-text">- So we need to make our learning, examing functions responsive on tablet and phone.</span>
						</span>
					</li>
					<li>
						Enterprise supervisor
						<span class="detail-description">
							- A department supervisor may use some performance, objective, or learning management function for their department when they are outside the office.
							<br />
							<span class="pri-text">-  So we need to make parts of our management functions be responsive on the tablet. </span>
						</span>
					</li>
				</ul>
				<div class="p-title-sub">
					Internal Users (LearningDigital's engineers)
				</div>
				<ul>
					<li>
						Boss
					</li>
					<li>
						Sales
					</li>
					<li>
						Customer Support PM
					</li>
					<li>
						Developers
					</li>
				</ul>
				
				<div class="p-title-sub">
					Analysis
				</div>
				<ul>
					<li>
						A design guideline is in the form of graphic, but an RWD design system like bootstrap or materialize.css are real HTML code in order to display dynamic responsive effect.
					</li>
					<li>
						There's not much public admin website design system accessible, however, some B2B product company would build UI guideline or design system for themselves.
					</li>
					<li>
						I found two company had shared their experiences of admin website design system, both of them divide the guideline into several levels.
					</li>
					<li>
						Some whole page could repeatedly occur on a different module that can be called "floorplan".
					</li>
					<li>
						Some component like pagination, search panel, or tab bar are also can be defined as a level that could be repeatedly used in different floorplans. 
					</li>
				</ul>
				
				<div class="row">
					<div class="col s6">
						<span class="img-note">An HCM UI guideline case study of a competitor in China</span>
						<img src="@/assets/img/Works/hcm-analysis-1.jpg" alt="" />
					</div>
					<div class="col s6">
						<span class="img-note">SAP - Pattern-based approach.</span>
						<img src="@/assets/img/Works/hcm-analysis-2.jpg" alt="" />
					</div>
					<div class="col s8 offset-s2">
						<span class="img-note">Survey of responsive admin website templates.</span>
						<img src="@/assets/img/Works/template-survey.jpg" alt="" />
					</div>
				</div>
				
				<div class="p-title-sub">
					Target
				</div>
				
				<ul>
					<li>
						Generate an RWD design system for admin system. 
					</li>
					<li>
						High reusability so that our PM can simply screenshot the floorplans and components in the design system to make a planning document and then directly pass to the developer without handled by UI designer. 
					</li>
					<li>
						Style customizability - Because of the HCM is installed in enterprises' intranet without sync with the motherboard,
						most buyers used to ask a custom color plan for their CIS. So this RWD design system should provide color plan editor. 
					</li>
				</ul>
				
			</div>
			<div id="article_design">
				<div class="p-title">
					Design
				</div>
				<div class="p-title-sub">
					Set Visual Goal
				</div>
				<ul>
					<li>
						We(designers) picked out some admin website template from Themeforset, screenshot layouts or components that we may use in our HCM. 
					</li>
					<li>
						We invited the boss, CTO, engineers, project manager 
											leader and a sales into a discussion, 
											displayed those themes and finally vote
											the preferred style for new HCM.
					</li>
					<li>
						According to the decision, we bought one UI template as the basis of design system.
					</li>
				</ul>
				<div class="row">
					<div class="col s12 ">
						<span class="img-note">Comparsion between several Bootstrap Templates.</span>
						<img src="@/assets/img/Works/hcm-analysis-3.jpg" alt="" />
					</div>
				</div>
				<div class="p-title-sub">
					Analysis Old product
				</div>
				<ul>
					<li>
						I broke down old HCM layout into components and units 
						so that we could found those repeatedly used pattern 
						then redesign them in the way of RWD with considering of the use cases.
					</li>
					<li>
						Condition Tree
					</li>
				</ul>
				<div class="row">
					<div class="col s6">
						<span class="img-note">Card sorting for analysis old version layout.</span>
						<img src="@/assets/img/Works/hcm-card-sort.jpg" alt="" />
					</div>
					<div class="col s6">
						<span class="img-note">Condition tree of file uploader.</span>
						<img src="@/assets/img/Works/hcm-old_ver-analysis.jpg" alt="" />
					</div>

				</div>
				
				<div class="p-title-sub">
					UI Proposal
				</div>
				<ul>
					<li>
						Wireframe
					</li>
					
					<div class="row">
						<div class="col s9">
							<img src="@/assets/img/Works/PC-layout-Tab1-Filter.jpg" alt="" />
							<span class="img-note"></span>
						</div>
						<div class="col s3">
							<img src="@/assets/img/Works/phone-layout-2-no-expend.jpg" alt="" />
							<span class="img-note"></span>
						</div>
					</div>
					<li>
						HTML Prototype
						
						<span class="detail-description">
						- At first, we tried to build prototypes by Axure. However, we soon realize that there are some UI components must be transformed by jquery in order to reach the cross-device requirement, for example, tabs or table width massive data
						</span>
						<span class="detail-description">
						- It would be fast and realizable to directly implement special jquery in HTML prototype than simply build an Axure prototype that we not sure if we can develop the effect after decision made.
						</span>
						<span class="detail-description">
						- So in this project, all of the prototypes are built by HTML/CSS/jquery and then given to internal users for the usability test.
						</span>
					</li>
					
				</ul>
				
				<div class="p-title-sub">
					User Test
				</div>
				
				<div class="row">
					<div class="col s6">
						<span class="img-note">Test for PC layout.</span>
						<img src="@/assets/img/Works/hcm-rwd-usertest-1.jpg" alt="" />
					</div>
					<div class="col s6">
						<span class="img-note">Test for smart phone layout.</span>
						<img src="@/assets/img/Works/hcm-rwd-usertest-2.jpg" alt="" />
					</div>
				</div>
				
				<div class="p-title-sub">
					UI Spec
				</div>
				<div class="row">
					<div class="col s12">
						<span class="img-note">UI spec of HCM color plan editor for those enterprises who want to have a color plan suit CIS.</span>
						<img src="@/assets/img/Works/hcm-ui-spec.jpg" alt="" />
					</div>
					<div class="col s12">
						<span class="img-note">A file uploader could have different optional functions according to scenes.</span>
						<img src="@/assets/img/Works/hcm-guideline-uploader.jpg" alt="" />
					</div>
				</div>
				
			</div>
			<div id="article_development">
				<div class="p-title">
					Development
				</div>
				<ul>
					<li>
						We start from a simple project to let engineers get familiar with RWD and the design system.
					</li>
					<li>
						We ran SCRUM, negotiated timely in the daily meeting.
					</li>
					<li>
						I taught engineers and precisely check if they can assembled layout right by copying the HTML code from the design system and combining by themselves.
					</li>
					<li>
						Sometime engineers would modify components into another program so that they can truly repeatedly use.
					</li>
				</ul>
				
			</div>
			<div id="article_improvement">
				<div class="p-title">
					Improvement
				</div>
				<div class="p-title-sub">
					Internal User Feedback
				</div>
				<ul>
					<li>
						We gained internal user feedback by asked PMs and Sales manipulating new UI watch their behavior and ask if they have any trouble.
				
					</li>
				</ul>
				
				<div class="p-title-sub">
					External User Feedback
				</div>
				<ul>
					<li>
						We set a demo site to invite potential clients to come and try our HCM functions.
					</li>
					<li>
						Our sales are responsible for finding potential customers and bring back their feedback.
					</li>
					<li>
						Because of our HCM system is set up in each enterprises' intranet, after customer really use new RWD UI product,
						we gained feedback by PM who take charge of enterprises.
					</li>
				</ul>
				
			</div>
			<div class="p-title">
				Outcome
			</div>
