<template>
  <div id="app">
    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css">


    <!-- navbar用v-if切換？ -->

    <transition  mode="out-in"
      :leave-active-class="leaveTransition"
      :enter-active-class="enterTransition">
      <!-- <keep-alive> -->
        <router-view/>
      <!-- </keep-alive> -->
    </transition>
    <Footer/>
  </div>
</template>

<script>
  import Footer from './components/Footer'
  export default {
    components: {
      Footer
    },
    data () {
      return {
        enterTransition: 'animated fadeIn',
        leaveTransition: 'animated fadeOut',
      }
    },
    watch: {
      '$route' (to, from) {
        let toDepth = to.meta.depth;
        let fromDepth = from.meta.depth;
        if (fromDepth > toDepth) {
          this.enterTransition = 'animated fadeIn';
          this.leaveTransition = 'animated fadeOutDown';
        } else if (fromDepth < toDepth) {
          this.enterTransition = 'animated fadeInUp';
          this.leaveTransition = 'animated fadeOut';
        } else {
          this.enterTransition = 'animated fadeIn';
          this.leaveTransition = 'animated fadeOut';
        }
      }
    }
  }
</script>
<style scoped>
  .animated{
    animation-delay: 0s!important;
  }
  .fadeOut{
    animation-duration: 0.25s!important;
  }
  .slideInRight,.slideInUp{
    animation-duration: 0.75s!important;
  }
</style>