<template>
	
	<div>
		<Nav mainTitle="OneAD廣告操作系統優化"/>
		<section class="article_container">
			<div id="article_requirment">
				<div class="p-title">
					緣起
				</div>
				<div class="">
					
					OneAD的主要服務是透過AI技術，將廣告主的影音廣告，依目標受眾(TA)的性別、年齡、興趣等屬性，發送至台灣各大主流媒體。
					<br />
					如此高彈性的廣告操作，需要由一專門的服務團隊，使用OneAD廣告操作系統來進行。
					<br />
					此系統在過去僅靠工程師獨立打造，因此在Usability上累積了許多問題。
					
					<br /><br />
					
					此專案是我在OneAD期間，透過多次迭代、逐步優化此廣告操作系統之過程。
					<br />
					包含三個主題：①動線優化 ②UI優化與增進一致性 ③建立持續性改善的機制
					
					
				</div>
				
				<div class="p-title">
					我的角色
				</div>
				<div class="">
					
					UI/UX Designer
					<br />
					<ul>
						<li>新功能需求訪談</li>
						<li>定期內部痛點改善會議主持</li>
						<li>使用者訪談</li>
						<li>UI設計</li>
						<li>Prototype製作</li>
					</ul>
					
					
				</div>
				<div class="p-title">
					Part I 動線優化
				</div>
				<div class="p-title-sub">
					概述
					
				</div>
				<div>
					原本的廣告操作系統，其IA很混亂，而且麵包屑被當成顯示狀態用；而側邊Menu則被當成巢狀目錄使用，使用者一層層深入目錄時，側邊欄竟會長出日期Filter。
				</div>
				<div class="row" style="">
					
					<div class="col s10 offset-s1" style="text-align:right;">
						<img src="@/assets/img/Works/ODM_before_change.png" alt="" style=""/>
					</div>
				</div>
				
				
				<div class="p-title-sub">
					掌握系統全貌並分析
				</div>
				
				<div>
					我對系統進行地毯式的截圖，並將各個畫面依路徑流程串起，
					下圖以便利貼的型式簡化截圖內容，排出整個網站的架構。
					<ul>
						<li>可以發現資料有層級關係：一個廣告主 → 多個廣告活動；一個廣告活動 → 多個投放項目；投放項目又會產生對應的成效報告</li>
						<li>檢視廣告活動時有兩種模式：①察看單一廣告主的廣告活動 ②察看所有的廣告活動，投放項目亦然。</li>
						<li>經訪談後了解到上述兩模式是給不同目的的User使用，對廣告操作人員而言，只需聚焦在單一廣告主所屬的廣告活動&投放項目；但對客服主管而言，卻是要定期檢視全部的廣告活動</li>
					</ul>
				</div>
				<div class="row">
					<div class="col s10 offset-s1" style="text-align:right;">
						<img src="@/assets/img/Works/ODM_UI_Flow.jpg" alt="" style=""/>
					</div>
				</div>
				<div>
					下圖中，我將系統各頁麵包屑與側邊Menu截取整理，可以看出其邏輯一致性不足、且帶有巢狀路徑變化，是令User混亂的最大原因。
					
				</div>
				<div class="row">
					<div class="col s10 offset-s1" style="text-align:right;">
						<img src="@/assets/img/Works/ODM_breadcrumbs_menu_before.jpg" alt="" style=""/>
					</div>
				</div>
				<div class="p-title-sub">
					Design Process
				</div>
				<div>
					我依據Design Thinking的雙鑽石模型經過以下步驟，產生解決方案：
					<ul>
						<li>Discover -
							<br />
							我接手開發團隊手上累積的issue(來自User的抱怨)，並親自操作測試機，了解系統的架構與issue的全貌，並進一步偵測更多Usability問題。
							<br />
							同時我對廣告客服部門進行一對一訪談，了解他們日常的使用情境與最常遭遇的操作痛點。
						</li>
						<li>Define -
							對issue進行優先度排序，依緊急程度與開發規模來評估，提出可能的解決思路。
							<br />
							而後召開與RD Leader及客服主管們的會議，確認我的優先目標(動線優化)與解決思路是否符合需求與商業邏輯、且在開發上是可行的。
						</li>
						<li>Develop - 
							<br />
							繪製Wireframe並製成可點擊之Prototype，讓User進行操作，確認他們不會因為適應了舊版的複雜動線後，反而在新版流程上迷失。
							<br />
							測試時邀請開發人員旁觀，確定技術的可行性，並爭取他們對解決方案的認同。
						</li>
						<li>Deliver -
							<br />
							將重新設計的元件(Breadcrumbs)，在各種情境中的資訊變化完整羅列；並將完整的UI Flow繪出，供RD參考。
							<br />
							改版上線後，再行追蹤User是否有適應不良，或其他未探知的動線問題。
						</li>
					</ul>
				</div>
				
				<div class="p-title-sub">
					解決方案
				</div>
				<div class="row">
					<div class="col s8 offset-s2" style="text-align:right;">
						<img src="@/assets/img/Works/ODM_breadcrumbs.jpg" alt="" style=""/>
					</div>
					<div class="col s10 offset-s1" style="text-align:right;">
						<img src="@/assets/img/Works/ODM-Nav_Full_UI Flow.jpg" alt="" style=""/>
					</div>
				</div>
				<div class="p-title-sub">
					成果
				</div>
				<div class="">
					<ul>
						<li>
							改版後的成果，得到客服部門、開發團隊、QA的一致好評，我們也在因此能架構之下，在各頁面安插「回上一層」與在水平層級間跳轉的快速路徑。
						</li>
						<li>
							同時系統各頁的頁址也根據新麵包屑的結構進行重整，這對我後續在使用GA統計各組功能的使用量時，有良好的幫助。
						</li>
						<li>
							在解決完重要的動線問題後，我們將能專注於局部頁面的UI優化，迭代式的翻新整個系統。
						</li>
					</ul>
				</div>
				

				<div class="p-title">
					Part II UI優化與增進一致性 
				</div>
				<div>
					在做了第一次，也是影響最大的動線優化後，我依業務上的優先順序進行多次的局部優化。
					<br />
					包含了：
					<ul>
						<li>
							廣告、投放項目等列表的UI修改，簡化Table的資訊並使其易於閱讀。
						</li>
						<li>
							調整廣告投放的設定表單UI，使單頁中UI的動線穩定從左上到右下；並讓各種元件能以最少的點墼來操作；減少頁面捲動的必要。
						</li>
						<li>
							統一不同頁面中相近用途的UI風格，讓Tab、Table、Button、Selectbox都能以同樣的CSS與操作邏輯呈現。
						</li>
					</ul>
					以下是我改版前後的對比：	
				</div>
				
