				<div class="p-title">
					Part III 建立持續性改善的機制
				</div>
				在多次的優化後，OneAD的廣告投放系統漸趨穩定表現。<br />
				我們於是轉為定期定量的蒐集優化的需求並改善之：
				<ul>
					<li>每隔2週，我與RD Leader、PM會和廣告客服人員開發，聽取他們需要的修改，並協商優先順序。</li>
					<li>我依然會照Design Thinking流程，訪談User的操作心得，並提出Wireframe或Prototype(若功能複雜)</li>
					<li>當有來自PM的大型修改(如: 新增某類廣告素材、某種報表)時，則另啟專案進行設計/開發。</li>
				</ul>
				
